* {
  margin: 0;
  padding: 0;
  text-decoration: 0;

  box-sizing: border-box;
}

.container {
  width: 100%;
  flex-shrink: 0;
  padding-top: 5px;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.81) 32.13%,
    rgba(0, 0, 0, 0) 100%
  );

  background-repeat: no-repeat;
  height: 100vh;
}

.box {
  padding-top: 150px;
}
