.flex-container {
  height: 721px;
  display: flex;
  margin-top: 60px;
  flex-direction: column;
  font-size: 30px;
  padding-top: 20px;
}

.flex-item-left {
  padding: 10px;
  flex: 50%;
  padding-left: 80px;
}

.flexleft1 {
  display: flex;
  justify-content: flex-start;
}

.text1 {
  /* position: fixed; */
  margin-left: 350px;
  width: 350px;
  padding-left: 0px;
  color: #fff;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.flex-item-right {
  margin-top: 100px;
  padding: 10px;
  flex: 50%;
  padding-left: 80px;
}
.text2 {
  width: 250px;
  height: 104px;
  flex-shrink: 0;
  box-shadow: 2px 2px 5px 0px rgba(255, 255, 255, 0.08);
}

.btn {
  display: flex;
  width: 249px;
  height: 61px;
  color: #fff;
  margin-top: 20px;
  padding-bottom: 0px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border: none;
  background-color: #8000ff;
}

.flex-item-right1 {
  margin-top: 70px;
  display: flex;
  justify-content: right;
  padding: 10px;
  flex: 50%;
  padding-left: 80px;
}


/* Responsive layout - makes a one column-layout instead of two-column layout */
@media (max-width: 800px) {
  .flex-container {
    flex-direction: column;
  }
  .text1 {
    margin-left: 0px;
    margin-top: 20px;
  }
  .flex-item-right1 {
    margin-top: 80px;
    display: flex;
    justify-content: center;
  }
}

.zoom {
  transition: transform 0.2s;
  flex-shrink: 0;
  width: 65px;
  height: 65px;
  margin: 0 auto;
  border-radius: 12px;
  border: 1px solid #a129ff;
  box-shadow: 0px 0px 16px 3px rgba(161, 41, 255, 0.25);
}

.zoomhover {
  width: 144px;
  height: 144px;
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
  transform: scale(1.5);
  transition: 1s;
  margin-right: 30px;
  margin-left: 30px;

  border-radius: 16px;
  border: 1px solid #a129ff;
  box-shadow: 0px 0px 16px 3px rgba(161, 41, 255, 0.25);
}

.zoom:active {
  width: 144px;
  height: 144px;
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
  transform: scale(1.5);
  transition: 1s;
}
